  text-decoration: underline;
}

.map-container .custom-marker {
  color: #fff;
  cursor: pointer;
  height: 37px;
  padding: 3px;
  width: 32px;
}

.marker-content {
  margin: 2px;
}

.map {
  float: right;
  height: 530px;
  width: 100%;
}

.gm-style .cluster img {
  border: 0;
  margin: 0;
  padding: 0;
}

.locator-list {
  display: flex;
  flex-direction: column;
}

@media (min-width: 576px) {
  .locator-list {
    flex-grow: 1;
    height: auto;
  }
}

@media (min-width: 576px) and (max-width: 991.98px) {
  .locator-list .list-label {
    margin-left: 0;
  }

  .locator-list .list-content {
    padding: 10px 0;
  }

  .locator-list .list-details {
    width: 140px;
  }
}

.locator-list .loc-list-container {
  flex-grow: 1;
  overflow-x: hidden;
  overflow-y: scroll;
}

.locator-list .list-label {
  background: #451400;
  color: #fff;
  float: left;
  font-weight: bold;
  margin: 10px 0 0 6px;
  padding: 2px 3px;
  text-align: center;
  width: 27px;
}

.locator-list .list-details {
  float: left;
  margin-left: 6px;
  width: 165px;
}

.locator-list .list-content {
  padding: 10px;
}

.locator-list .loc-dist {
  color: #8e8e8e;
  font-style: italic;
  font-weight: bold;
}

.locator-list .list-location {
  border: 1px solid transparent;
  box-sizing: border-box;
  clear: left;
  cursor: pointer;
  display: block;
  float: left;
  margin: 0;
  width: 100%;
}

.locator-list .list-location.first {
  margin-top: 0;
}

.locator-list .list-location.last {
  margin-bottom: 0;
}

.locator-list .list-location.even {
  background-color: #eeeeee;
}

.locator-list .list-location.focus {
  border: 1px solid rgba(82, 168, 236, 0.9);
  box-shadow: 0 0 8px rgba(82, 168, 236, 0.7);
  transition: border .2s linear 0s, box-shadow .2s linear 0s;
  z-index: 1;
}

.list-pagination .pagination {
  padding-left: 0;
  list-style: none;
  border-radius: 0.25rem;
  display: flex;
  justify-content: center;
  margin-bottom: 0;
  margin-top: 10px;
}

.list-pagination .pagination .page-link {
  cursor: pointer;
  display: block;
  line-height: 1.25;
  margin-left: -1px;
  padding: 0.5rem 0.75rem;
  position: relative;
  user-select: none;
}

.locator-loading .loading {
  background: rgba(255, 255, 255, 0.75);
  bottom: 0;
  display: block;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: visibility 0s, opacity .5s linear;
  visibility: hidden;
}

.locator-loading .loading.show {
  opacity: 1;
  visibility: visible;
}

.locator-loading .loading-content {
  padding-top: 30px;
  position: absolute;
  text-align: center;
  top: 20%;
  width: 100%;
}

.locator-loading .loading-content .spinner {
  background: url("../images/ajax-loader.gif") no-repeat scroll center/25px 25px;
  height: 25px;
  margin: auto;
  padding: 5px;
  width: 25px;
}

.locator-loading .loading-content span {
  font-size: large;
  font-weight: bold;
}




