.locator-loading {

  .loading {
    background: $transparent-white;
    bottom: 0;
    display: block;
    left: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: visibility 0s, opacity .5s linear;
    visibility: hidden;

    &.show {
      opacity: 1;
      visibility: visible;
    }
  }

  .loading-content {
    padding-top: 30px;
    position: absolute;
    text-align: center;
    top: 20%;
    width: 100%;

    .spinner {
      background: url('../../images/ajax-loader.gif') no-repeat scroll center / 25px 25px;
      height: 25px;
      margin: auto;
      padding: 5px;
      width: 25px;
    }

    span {
      font-size: large;
      font-weight: bold;
    }
  }
}



